import React, { forwardRef } from "react";
import ReactModal from "react-modal";

import { getCustomModalStyles } from "./Modal.styles";
import { ModalProps } from "./Modal.types";
import { ModalContext } from "./ModalContext";

/**
 * @description Modal is built on top of react-modal and the modal props extends the react-modal props
 * @see [react-modal](https://github.com/reactjs/react-modal) for more information
 */
const Modal = forwardRef<ReactModal, ModalProps>(({
	dataQA,
	children,
	variant = "basic",
	onRequestClose,
	appElement,
	preventBackgroundScroll = true,
	size,
	isOpen,
	className,
	...props
}, ref) => {
	const disableScroll = () => {
		if (preventBackgroundScroll) document.body.style.overflow = "hidden";
	};
	const enableScroll = () => {
		if (preventBackgroundScroll) document.body.style.overflow = "unset";
	};
	return (
		<ModalContext.Provider value={{ onRequestClose, variant, size }}>
			<ReactModal
				appElement={appElement}
				style={getCustomModalStyles({ size, variant })}
				onRequestClose={onRequestClose}
				data={{
					qa: dataQA,
				}}
				onAfterOpen={disableScroll}
				onAfterClose={enableScroll}
				ref={ref}
				isOpen={isOpen}
				className={className}
				{...props}
			>
				{children}
			</ReactModal>
		</ModalContext.Provider>
	);
});

Modal.displayName = "Modal";

export default Modal;
